import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';
import { MarkdownComponent } from 'ngx-markdown';
import { map } from 'rxjs/operators';
import { Article } from 'src/app/models/article.model';
import { User } from 'src/app/models/user.model';
import { ArticleService } from 'src/app/services/article.service';
import { CookieCatcherService } from 'src/app/services/cookie-catcher.service';
import { LogsService } from 'src/app/services/logs.service';
import { UserService } from 'src/app/services/user.service';

@Component({
    selector: 'app-articles-details',
    templateUrl: './articles-details.component.html',
    styleUrls: ['./articles-details.component.css']
})

export class ArticlesDetailsComponent implements OnInit {
    id = 0;
    articleData?: Article[];
    userData?: User[];
    userKey?: string;
    public = 
    {
        key: '',
        id: '',
        title: '',
        artisan: '',
        collaborator: '',
        source: '',
        content: '',
        serires: '',
        season: 0,
        created_date: '',
        updated_date: '',
        views: 0,
        comments: 0,
        ban: false,
        view: false,
    };

    userInfo = {
        key: '',
        nickname: '',
    };

    logID: number = 0;
    returnValue: number = 0;
    viewRegister?: Article[];

    constructor(
        private ArticleService: ArticleService,
        private route: ActivatedRoute,
        private CookieService: CookieService,
        private UserService: UserService,
        public CookieCatcherService: CookieCatcherService,
        private LogsService: LogsService
    ) {
        this.userKey = this.CookieService.get('userKey');
    }

    ngOnInit(): void 
    {
        this.getSingleArticle();
        this.getUser();
    }

    createLog(): void
    {
        this.logID = Number(this.route.snapshot.paramMap.get('id'));
        const viewLog = {
            viewID: this.logID,
            userKey: this.userKey || 'anonymous',
            viewTime: String(new Date())
        };
        this.LogsService.create(viewLog)
            .then( () => {
                console.warn('紀錄');
            });
        
        this.watchAllview();
    }

    getUser(): void
    {
        this.UserService.getAll().snapshotChanges()
            .pipe(
                map(changes => 
                    changes.map(c =>
                        ({ key: c.payload.key, ...c.payload.val() })
                    )
                ),
                // map(data =>
                //     data.filter(c =>
                //         c.key === this.userKey
                //     )
                // )
            )
            .subscribe(res => {
                // console.log(res);
                this.userData = res;
                if(this.userData.length > 0)
                {
                    this.userInfo.key = String(this.userData[0].key);
                    this.userInfo.nickname = String(this.userData[0].nickname);    
                }
            })
    }

    getSingleArticle(): void
    {
        this.id = Number(this.route.snapshot.paramMap.get('id'));

        this.ArticleService.getAll().snapshotChanges()
        .pipe(
            map(changes => 
                changes.map(c =>
                    ({ key: c.payload.key, ...c.payload.val() })
                )
            ),
            map(data =>
                data.filter(c =>
                    c.id === this.id
                )
            )
        )
        .subscribe(res => {
            this.articleData = res;
            // console.log(this.articleData);

            // 手動完成
            this.public.key = String(this.articleData[0].key);
            this.public.id = String(this.articleData[0].id);
            this.public.title = String(this.articleData[0].title);
            this.public.artisan = String(this.articleData[0].artisan);
            this.public.collaborator = String(this.articleData[0].collaborator);
            this.public.source = String(this.articleData[0].source);
            this.public.content = String(this.articleData[0].content);
            this.public.serires = String(this.articleData[0].serires);
            this.public.season = Number(this.articleData[0].season);
            this.public.updated_date = this.articleData[0].updated_date;
            this.public.created_date = this.articleData[0].created_date;
            this.public.ban = Boolean(this.articleData[0].ban);
            this.public.view = Boolean(this.articleData[0].view);
        });
        this.createLog();
    }

    checkLogin(): boolean
    {
        return this.CookieCatcherService.checkUserLogin();
    }

    watchAllview(): void
    {
        this.logID = Number(this.route.snapshot.paramMap.get('id'));

        this.LogsService.getAll().snapshotChanges()
        .pipe(
            map(changes => 
                changes.map(c =>
                    ({ key: c.payload.key, ...c.payload.val() })
                )
            ),
            map(data =>
                data.filter(c =>
                    c.viewID === this.logID
                )
            )
        )
        .subscribe(res => {
            this.viewRegister = res;
            this.returnValue = Number(this.viewRegister.length);
        });
    }
}
