import { Component, OnInit } from '@angular/core';
import { map } from 'rxjs/operators';
import { Article } from 'src/app/models/article.model';
import { ArticleService } from 'src/app/services/article.service';
import { CookieCatcherService } from 'src/app/services/cookie-catcher.service';
import { LogsService } from 'src/app/services/logs.service';

@Component({
    selector: 'app-articles-view',
    templateUrl: './articles-view.component.html',
    styleUrls: ['./articles-view.component.css']
})

export class ArticlesViewComponent implements OnInit {
    ArticleData?: Article[];
    currentArticle?: Article;
    ArticleDataSerires?: Article[];

    pageLength: number = 0;
    pageIndex: number = 0;
    pageMax: number = 0;

    // count using
    returnValue: number = 0;
    viewRegister?: Article[];

    constructor(
        private ArticleService: ArticleService,
        public CookieCatcherService: CookieCatcherService,
        private LogsService: LogsService
    ) { }

    ngOnInit(): void
    {
        this.retrieveArticle();
    }

    refreshList(): void
    {
        this.retrieveArticle();
    }

    retrieveArticle(): void
    {
        this.ArticleService.getAll()
            .snapshotChanges()
            .pipe(
                map(changes => 
                    changes.map(c => 
                        ({key: c.payload.key, ...c.payload.val() })
                    )
                )
            )
            .subscribe(data => {
                this.ArticleData = data;

                this.ArticleData.reverse();
                this.pageLength = this.ArticleData.length;
                this.ArticleData = this.ArticleData.slice(this.pageIndex*10, (this.pageIndex+1)*10);
                this.pageMax = Math.floor((this.pageLength-1) / 10) + 1;
    
                const set = new Set();
                this.ArticleDataSerires = this.ArticleData.filter(item => !set.has(item.serires)?set.add(item.serires):false)
            });
    }

    root(): boolean
    {
        return this.CookieCatcherService.checkUserRoot();
    }

    filterArticles(serires: string | any): void
    {        
        this.ArticleService.getAll()
        .snapshotChanges()
        .pipe(
            map(changes => 
                changes.map(c => 
                    ({key: c.payload.key, ...c.payload.val() })
                )
            ),
            map(data =>
                data.filter(c =>
                    c.serires === serires
                )
            )
        )
        .subscribe(data => {
            this.ArticleData = data;
                
            this.ArticleData.reverse();
            this.pageLength = this.ArticleData.length;
            this.ArticleData = this.ArticleData.slice(this.pageIndex*10, (this.pageIndex+1)*10);
            this.pageMax = Math.floor((this.pageLength-1) / 10) + 1;

            const set = new Set();
            this.ArticleDataSerires = this.ArticleData.filter(item => !set.has(item.serires)?set.add(item.serires):false)
        });
    }

    changePages(page: boolean): void
    {
        if(page){
            if((this.pageIndex + 1)*10 < this.pageLength)
            {
                this.pageIndex += 1;
            }
        }
        if(!page){
            if((this.pageIndex - 1)*10 >= 0)
            {
                this.pageIndex -= 1;
            }
        }
        this.retrieveArticle();
    }

}
